/*   -------------   social   -------------   */
.social {
    &__list {
        display: flex;
        column-gap: 10px;
    }

    &__link {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 35px;
        min-width: 35px;
        max-width: 35px;
        background-color: white;
        border-radius: 50%;
    }

}